/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { sentenceCase, capitalCase } from 'change-case';
import { useState, useEffect, } from 'react';
import { useSnackbar } from 'notistack';
// @mui
import { useTheme } from '@mui/material/styles';
import {
    Card,
    Table,
    TableRow,
    TableBody,
    TableCell,
    Container,
    Typography,
    TableContainer,
    TablePagination,
    Switch,
    Tabs,
    Tab,
    Button
} from '@mui/material';

// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_ADMIN, PATH_DASHBOARD } from '../../../routes/paths';
// hooks
import useSettings from '../../../hooks/useSettings';
// components
import Page from '../../../components/Page';
import Label from '../../../components/Label';
import Scrollbar from '../../../components/Scrollbar';
import SearchNotFound from '../../../components/SearchNotFound';

import HeaderBreadcrumbs from '../../../components/HeaderBreadcrumbs';
// format number 
import { fCurrency, } from '../../../utils/formatNumber';
// sections
import { Link as RouterLink } from 'react-router-dom';
import { TransHead, PlanToolbar, Payment } from '../../../sections/admin/user/list';


// axios
import axios from '../../../utils/axios';

// ----------------------------------------------------------------------

const TABLE_HEAD = [
    { id: 'sender', label: 'Sender', alignRight: false },
    { id: 'recipient', label: 'Recipient', alignRight: false },
    { id: 'reference', label: 'Reference', alignRight: false },
    { id: 'amount', label: 'Amount', alignRight: false },
    { id: 'charge', label: 'Charge', alignRight: false },
    { id: 'oldbal', label: 'Old Bal', alignRight: false },
    { id: 'newbal', label: 'New Bal', alignRight: false },
    { id: 'narration', label: 'Narration', alignRight: false },
    { id: 'created_at', label: 'Date', alignRight: false },
    { id: 'status', label: 'Status', alignRight: false },
    { id: 'invoice', label: 'Invoice', alignRight: false },
    { id: 'action', label: 'Action', alignRight: false },
];

const ACCOUNT_TABS = [
    { value: 'ALL', label: 'ALL' },
    { value: 'SUCCESS', label: 'Successful' },
    { value: 'PENDING', label: 'Pending' },
    { value: 'FAILED', label: 'Failed' }
];

// ----------------------------------------------------------------------

export default function AdminInternalTransferTransaction() {
    const theme = useTheme();
    const { themeStretch } = useSettings();
    const [userList, setUserList] = useState([]);
    const [page, setPage] = useState(0);
    const [load, SetLoad] = useState(true);
    const [totalPage, SetTotal] = useState(0);
    const [filterName, setFilterName] = useState('');
    const [rowsPerPage, setRowsPerPage] = useState(10);
    const [dense, Setdense] = useState(false);
    const [isNotFound, SetNotFound] = useState(false);
    const [status, setStatus] = useState('ALL');

    const AccessToken = window.localStorage.getItem('accessToken');
    const { enqueueSnackbar } = useSnackbar();

    useEffect(() => {
        initialize(page, rowsPerPage, status, filterName);
    }, []);

    const initialize = async (pag, limit = 10, status, search) => {
        let current_page = pag + 1;
        SetLoad(true);
        try {
            const response = await axios.get(`/api/admin/all/internal/transfers/${AccessToken}/secure?page=${current_page}&limit=${limit}&status=${status}&search=${search}`)

            setUserList(response.data?.transfers?.data || []);
            SetTotal(response.data?.transfers?.total || 0)
            SetNotFound((response.data?.transfers?.total || 0) === 0)
            setPage(pag);
            SetLoad(false);

        } catch (_) {
            SetLoad(false);
            setUserList([])
        }
    }

    const handleChangeRowsPerPage = (event) => {
        const limit = parseInt(event.target.value, 10);
        setRowsPerPage(limit);
        initialize(0, limit, status, filterName);
        setPage(0);
    };

    const handleFilterByName = async (filterName) => {
        setFilterName(filterName);
        setPage(0);
        initialize(0, rowsPerPage, status, filterName);
    };

    function handleChangeDense(event) {
        Setdense(event.target.checked);
    }

    const onFail = (transid) => {
        SetLoad(true);
        axios.post(`/api/admin/internal/refund/${AccessToken}/secure`, {
            transid
        }).then((res) => {
            SetLoad(false);
            enqueueSnackbar(res.data.message || 'Refund successful');
            initialize(page, rowsPerPage, status, filterName);
        }).catch((error) => {
            SetLoad(false);
            initialize(page, rowsPerPage, status, filterName);
            if (error.data?.message) {
                enqueueSnackbar(error.data.message, { variant: 'error' });
            } else if (error?.message) {
                enqueueSnackbar(error.message, { variant: 'error' });
            } else {
                enqueueSnackbar('An Error Occurred', { variant: 'error' });
            }
        });
    };

    const onSuccess = (transid) => {
        SetLoad(true);
        axios.post(`/api/admin/internal/success/${AccessToken}/secure`, {
            transid
        }).then((res) => {
            SetLoad(false);
            enqueueSnackbar(res.data.message || 'Marked as successful');
            initialize(page, rowsPerPage, status, filterName);
        }).catch((error) => {
            SetLoad(false);
            initialize(page, rowsPerPage, status, filterName);
            if (error.data?.message) {
                enqueueSnackbar(error.data.message, { variant: 'error' });
            } else if (error?.message) {
                enqueueSnackbar(error.message, { variant: 'error' });
            } else {
                enqueueSnackbar('An Error Occurred', { variant: 'error' });
            }
        });
    };

    return (
        <Page title="Transaction: Internal Transfers">
            <Container maxWidth={themeStretch ? false : 'lg'}>
                <HeaderBreadcrumbs
                    heading="All Internal Transfer Transactions"
                    links={[
                        { name: 'Dashboard', href: PATH_ADMIN.general.app },
                        { name: 'List' },
                    ]}
                />

                <Card>
                    <Tabs
                        value={status}
                        scrollButtons="auto"
                        variant="scrollable"
                        allowScrollButtonsMobile
                        onChange={(e, value) => { setStatus(value); initialize(0, rowsPerPage, value, filterName); setPage(0); }}
                    >
                        {ACCOUNT_TABS.map((tab) => (
                            <Tab disableRipple key={tab.value} label={capitalCase(tab.label)} value={tab.value} />
                        ))}
                    </Tabs>
                    <PlanToolbar
                        filterName={filterName}
                        onFilterName={handleFilterByName}
                    />

                    <Scrollbar>
                        <TableContainer sx={{ minWidth: 800 }}>
                            <Table size={dense ? 'small' : 'medium'}>
                                <TransHead headLabel={TABLE_HEAD} rowCount={userList.length} />

                                {!load ? (
                                    <TableBody>
                                        {userList.map((row) => {
                                            const { id, sender_username, recipient_username, reference, amount, charge, status: rowStatus, oldbal, newbal, message, created_at } = row;

                                            return (
                                                <TableRow hover key={id} tabIndex={-1}>
                                                    <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                                                        <Typography variant="subtitle2" noWrap>{sender_username}</Typography>
                                                    </TableCell>
                                                    <TableCell align='left'><Typography variant="subtitle2" noWrap>{recipient_username}</Typography></TableCell>
                                                    <TableCell align="left">{reference}</TableCell>
                                                    <TableCell align="left">₦{fCurrency(amount)}</TableCell>
                                                    <TableCell align="left">₦{fCurrency(charge || 0)}</TableCell>
                                                    <TableCell align="left">₦{fCurrency(oldbal)}</TableCell>
                                                    <TableCell align="left">₦{fCurrency(newbal)}</TableCell>
                                                    <TableCell align="left">{message || 'Internal Transfer'}</TableCell>
                                                    <TableCell align="left">{new Date(created_at).toLocaleString()}</TableCell>
                                                    <TableCell align="left">
                                                        <Label
                                                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                                                            color={(rowStatus === 'SUCCESS' ? 'success' : rowStatus === 'PENDING' ? 'warning' : 'error')}
                                                        >
                                                            {sentenceCase(rowStatus)}
                                                        </Label>
                                                    </TableCell>
                                                    <TableCell align='left'>
                                                        <Button
                                                            variant="contained"
                                                            component={RouterLink}
                                                            to={`${PATH_DASHBOARD.general.invioce}/${reference}/internal`}
                                                            size="small"
                                                            target='_blank'
                                                        >
                                                            Invoice
                                                        </Button>
                                                    </TableCell>
                                                    <TableCell align='right'>
                                                        <Payment
                                                            plan_status={(rowStatus === 'SUCCESS' ? 1 : rowStatus === 'PENDING' ? 0 : 2)}
                                                            onFail={() => onFail(reference)}
                                                            onSuccess={() => onSuccess(reference)}
                                                        />
                                                    </TableCell>
                                                </TableRow>
                                            );
                                        })}
                                    </TableBody>
                                ) : (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={12} sx={{ py: 3 }}>
                                                <Typography variant="body2">Loading ...</Typography>
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}
                                {isNotFound && (
                                    <TableBody>
                                        <TableRow>
                                            <TableCell align="center" colSpan={12} sx={{ py: 3 }}>
                                                <SearchNotFound searchQuery={filterName} />
                                            </TableCell>
                                        </TableRow>
                                    </TableBody>
                                )}
                            </Table>
                        </TableContainer>
                    </Scrollbar>

                    <FormControlLabel
                        control={<Switch checked={dense} onChange={handleChangeDense} />}
                        label="Dense padding"
                        sx={{ pl: 2 }}
                    />

                    <TablePagination
                        rowsPerPageOptions={[5, 10, 25, 100]}
                        component="div"
                        count={totalPage}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={(e, p) => initialize(p, rowsPerPage, status, filterName)}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                    />
                </Card>
            </Container>
        </Page>
    );
}
